async function connectwallet() {
  try {
    if (window.ethereum) {
      var web3 = new Web3(window.ethereum);
      await window.ethereum.send('eth_requestAccounts');
      var accounts = await web3.eth.getAccounts();
      if (accounts && accounts.length > 0) {
        account = accounts[0];
        document.getElementById('wallet-address').textContent = account;
        contract = new web3.eth.Contract(ABI, ADDRESS);
      } else {
        throw new Error('No accounts found.');
      }
    } else {
      throw new Error('Ethereum not detected.');
    }
  } catch (error) {
    console.error('Error connecting wallet:', error);
    window.alert('Error connecting wallet: ' + error.message);
    // Handle error - maybe show an error message to the user
  }
}

async function mint() {
  try {
    if (window.ethereum) {
      if (!account || !contract) {
        throw new Error('Wallet not connected or contract not initialized.');
      }
      var mintRate = Number(await contract.methods.price().call());
      await contract.methods.Mint().send({ from: account, value: String(mintRate) });
      window.alert('Minting successful!');
    } else {
      throw new Error('Ethereum not detected.');
    }
  } catch (error) {
    console.error('Error minting:', error);
    window.alert('Error minting: ' + error.message);
    // Handle error - maybe show an error message to the user
  }
}
  
